import dotenv from 'dotenv';
import { Map, Popup, NavigationControl, FullscreenControl } from 'maplibre-gl';
import { once, random } from 'lodash';
import { getCountryList, getUSMainBbox } from './countries';
import { createContriesSelector } from './countriesSelector';
import { createMapStyle } from './mapStyle'; 
import { queryDispatcher } from './sparql';
import 'maplibre-gl/dist/maplibre-gl.css';

dotenv.config();
const apiKey = `${process.env.API_KEY}`;

const initialState = { lng: -90, lat: 32, zoom: 3 };

let hoveredStateId = null;
let admLevel = 0;
let countries;

const colors = [
  '#F1175D',
  '#05D0DF',
  '#03A1C4',
  '#FBC935',
  '#FFAA01',
  '#FB3A1B',
  '#761FE8',
  '#593895'
];

const colorsGeneral = {
  background: '#DFF1F7',
  snow: '#FFFFFF',
  shadow: '#B1DDF9',
  outline: '#EEEEEE',
  land: '#D3DBEC'
}

const baseStyle = createMapStyle(colorsGeneral);

const map = new Map({
  container: 'map',
  style: baseStyle, // stylesheet location
  center: [initialState.lng, initialState.lat],
  zoom: initialState.zoom,
  maxZoom: 11,
  hash: true
});

map.addControl(new NavigationControl());

const featuresData = {};

let pinnedPopup = false;
// Create a popup, but don't add it to the map yet.
const popup = new Popup({
  closeButton: false,
  closeOnClick: false
});

const pinPopup = new Popup({
  closeButton: true,
  closeOnClick: false
});
pinPopup.on('open', function () {
  pinnedPopup = true;
});
pinPopup.on('close', function () {
  pinnedPopup = false;
});

map.on('load', () => {
  const layers = map.getStyle().layers;
  // Find the index of the first symbol layer in the map style
  let firstSymbolId;
  for (let i = 0; i < layers.length; i++) {
    if (layers[i].type === 'symbol') {
      firstSymbolId = layers[i].id;
      break;
    }
  }

  map.addLayer({
    'id': 'administrative-data',
    'type': 'fill',
    'source': 'countries',
    'source-layer': 'administrative',
    'layout': {},
    'paint': {
      'fill-color': [
        'coalesce',
        ['feature-state', 'color'],
        'rgba(0,0,0,0)'
      ],
      'fill-opacity': 1,
      'fill-outline-color': colorsGeneral.outline
    },
    'filter': ['==', 'level', admLevel]
  }, firstSymbolId);

  map.addLayer({
    'id': 'postal-data',
    'type': 'fill',
    'source': 'countries',
    'source-layer': 'postal',
    'layout': {
      "visibility": "none"
    },
    'paint': {
      'fill-color': [
        'coalesce',
        ['feature-state', 'color'],
        'rgba(0,0,0,0)'
      ],
      'fill-outline-color': colorsGeneral.outline,
      'fill-opacity': [
        'case',
        ['boolean', ['feature-state', 'hover'], false],
        1,
        0.7
      ]
    },
    'filter': ['==', 'level', admLevel]
  }, firstSymbolId);

  //selected country highlight
  map.addLayer({
    'id': 'adm0-boundary',
    'type': 'line',
    'source': 'countries',
    'source-layer': 'administrative',
    'layout': {
      'line-cap': "round",
      'line-join': "round"
    },
    'paint': {
      'line-color': '#000000',
      'line-blur': 0.5,
      'line-opacity': 1,
      'line-width': { stops: [[1, 1], [10, 3], [22, 6]] }
    },
    'filter': ['all', ['==', ['get', 'level'], 0], ['==', ['get', 'iso_a2'], 'XXXX']]
  }, firstSymbolId);

  addEventsToLayer('administrative-data', 'countries', 'administrative');
  addEventsToLayer('postal-data', 'countries', 'postal');

  map.on('sourcedata', function (e) {
    if (e.isSourceLoaded) {
      const features = map.querySourceFeatures('countries', { sourceLayer: 'administrative' });
      features.forEach(function (feature) {
        if (!map.getFeatureState({ id: feature.id, source: 'countries', sourceLayer: 'administrative' }).color) {
          map.setFeatureState({ id: feature.id, source: 'countries', sourceLayer: 'administrative' }, { color: makeFeatureColor(feature, colors, colorsGeneral.snow) });
        }
      });

      const featuresPostal = map.querySourceFeatures('countries', { sourceLayer: 'postal' });
      featuresPostal.forEach(function (feature) {
        if (!map.getFeatureState({ id: feature.id, source: 'countries', sourceLayer: 'postal' }).color) {
          map.setFeatureState({ id: feature.id, source: 'countries', sourceLayer: 'postal' }, { color: makeRandomColor(colors) });
        }
      });
    }
  });

});

map.addControl(new FullscreenControl());

const radios = document.querySelectorAll('input[type=radio][name="boundaryLevel"]');
radios.forEach(radio => radio.addEventListener('change', (event) => {
  admLevel = event.target.value;
  if (admLevel.includes('adm')) {
    admLevel = parseInt(admLevel.substring(3));
    map.setLayoutProperty('postal-data', 'visibility', 'none');
    map.setLayoutProperty('administrative-data', 'visibility', 'visible');
    map.setFilter('administrative-data', ['==', ['get', 'level'], admLevel]);
  } else {
    admLevel = parseInt(admLevel.substring(3)) - 3;
    map.setLayoutProperty('postal-data', 'visibility', 'visible');
    map.setLayoutProperty('administrative-data', 'visibility', 'none');
    map.setFilter('postal-data', ['==', ['get', 'level'], admLevel]);
  }
}));
document.querySelector('#zoom-to-usa > button').addEventListener('click', (evt) => {
  evt.preventDefault();
  evt.stopImmediatePropagation();
  zoomToUSAPostalCodes();
});

function zoomToUSA() {
  const bbox = getUSMainBbox();
  map.fitBounds(bbox);
}

function zoomToUSAPostalCodes() {
  const bbox = getUSMainBbox();
  const newCameraTransform = map.cameraForBounds(bbox);
  map.flyTo({center: newCameraTransform.center, zoom: 6});
}

function makeFeatureColor(feature, colors, defaultColor) {
  if (isCountry(feature)) {
    const group = getCountryGroup(feature);
    return (group !== -1 && group !== 8) ? colors[group] : defaultColor;
  } else if (isState(feature)) {
    const group = getCountryGroup(feature);
    return (group !== -1 && group !== 8) ? colors[group] : defaultColor;
  } else  {
    return makeRandomColor(colors);
  } 
} 

function isCountry(feature) {
  return (feature.properties.level === 0 && feature.properties.iso_a2) ? true : false;
}

function isState(feature) {
  return (feature.properties.level === 1 && feature.properties.iso_a2) ? true : false;
}

function getCountryGroup(feature) {
  if (!feature) { return -1 }
  const iso_a2 = feature.properties.iso_a2;
  switch (true) {
    case ["AM", "AG", "AU","BT","CA","CG","CZ","GH","GN","HT","IS","JO","KH","KR","LV","MT","ME","MZ","PE","SG","SV","SO","TJ","TV","UA","WS"].includes(iso_a2):
      return 0;
    case ["AZ","BD","CL","CM","DE","DJ","GY","HU","JM","LB","LY","LS","MG","MK","MN","MR","NU","NZ","PN","PF","SA","SH","ST","TT","UG","UZ","ZM"].includes(iso_a2):
      return 1;
    case ["AO","AS","TF","BI","BF","BG","BZ","BR","CN","CR","ES","HK","HR","ID","IR","IL","KN","LR","LC","MO","MU","NO","PW","PL","PR","SD","TN","UM","US","VI","VU"].includes(iso_a2):
      return 2;
    case ["AE","AR","BS","CI","DM","ET","GA","GD","HM","IN","IO","IE","IQ","IT","XK","LU","MX","NA","NE","PH","PT","RU","SN","SR","TZ","VA"].includes(iso_a2):
      return 3;
    case ["AT","BE","BH","BM","BB","DZ","EE","FK","GM","GU","HN","JE","KG","LI","MF","MD","NG","NR","SB","RS","SZ","TH","TR","VE","VG"].includes(iso_a2):
      return 4;
    case ["AI","BA","BL","BN","CF","CH","KM","CV","CU","EC","FM","GB","GE","KE","LT","MA","MC","MV","NF","NP","PG","PY","QA","SL","PM","SC","TC","TM","TL","VN","YE","ZW"].includes(iso_a2):
      return 5;
    case ["AW","AL","AD","BO","CD","CW","KY","CY","EG","FJ","GG","IM","KZ","KW","LA","ML","MP","MS","MY","NI","NL","PK","PA","KP","RO","GS","SI","SE","TG","TW","VC","ZA"].includes(iso_a2):
      return 6;
    case ["AF","BJ","BQ","BW","BY","CK","CO","DO","EH","ER","FI","FO","FR","DK","GF","GP","GQ","GR","GT","GW","JP","KI","LK","MM","MQ","MW","NC","OM","PS","RE","RW","SJ","SK","SM","SS","SX","SY","TD","TK","TO","UY","WF","YT"].includes(iso_a2):
      return 7;
    case ["AQ", "GL"].includes(iso_a2):
        return 8;
    default:
      return -1;
  }
}

function makeRandomColor(colors) {
  return colors[Math.floor(Math.random() * colors.length)];
}

const boundarySelector = document.getElementById('boundarySelector');
boundarySelector.addEventListener('change', (event) => {
  if (event.target.checked) {
    document.getElementById('zip').style.display = '';
    document.getElementById('adm').style.display = 'none';
    document.getElementById('label-only-usa').style.display = '';
    document.getElementById('zoom-to-usa').style.display = '';
  } else {
    document.getElementById('adm').style.display = '';
    document.getElementById('zip').style.display = 'none';
    document.getElementById('label-only-usa').style.display = 'none';
    document.getElementById('zoom-to-usa').style.display = 'none';
  }
});

(async() => {
  const countriesList = await getCountryList();
  countries = countriesList;
  const searchContainer = document.getElementById('searchContainer');
  createContriesSelector(searchContainer, countriesList, (selectedCountry, countryCode) => {
    if (countryCode === 'US') {
      zoomToUSA();
    } else {
      const { bbox } = selectedCountry;
      map.fitBounds([[bbox[0], bbox[1]], [bbox[2], bbox[3]]]);
    }
    map.setFilter('adm0-boundary', ['all', ['==', ['get', 'level'], 0], ['==', ['get', 'iso_a2'], countryCode]]);
  });
})();

function addEventsToLayer(layerId, source, sourceLayer) {
  // When the user moves their mouse over the state-fill layer, we'll update the
  // feature state for the feature under the mouse.
  map.on('mousemove', layerId, function (e) {
    if (e.features.length > 0 && !pinnedPopup) {
      const feature = e.features[0];
      const coordinates = e.lngLat;
      const description = createDescription(feature);
      if (hoveredStateId) {
        map.setFeatureState(
          { source: source, sourceLayer: sourceLayer, id: hoveredStateId },
          { hover: false }
        );
      }
      hoveredStateId = e.features[0].id;
      map.setFeatureState(
        { source: source, sourceLayer: sourceLayer, id: hoveredStateId },
        { hover: true }
      );
      popup.setLngLat(coordinates).setDOMContent(description).addTo(map);
    }
  });

  // When the mouse leaves the state-fill layer, update the feature state of the
  // previously hovered feature.
  map.on('mouseleave', layerId, function () {
    if (hoveredStateId) {
      map.setFeatureState(
        { source: source, sourceLayer: sourceLayer, id: hoveredStateId },
        { hover: false }
      );
    }
    hoveredStateId = null;
    popup.remove();
  });

  map.on('click', layerId, function (e) {
    const feature = e.features[0];
    const coordinates = e.lngLat;
    if (e.features.length > 0) {
      popup.remove();
      const description = createDescription(feature);
      pinPopup.setLngLat(coordinates).setDOMContent(description).addTo(map);
    }
  });

}

function createDescription(feature) {
  if (featuresData[feature.id]) {
    return featuresData[feature.id];
  } else {
    const outerDiv = document.createElement("div");
    outerDiv.setAttribute(
      "class",
      "popup-info"
    );

    const div1 = document.createElement("div");
    div1.setAttribute("class", "txt-name");
    const div1Text = document.createTextNode(createNameText(feature));
    div1.appendChild(div1Text);
    outerDiv.appendChild(div1);

    const div2 = document.createElement("div");
    div2.setAttribute("class", "txt-label");
    const div2Text = document.createTextNode(`Code `);
    div2.appendChild(div2Text);
    outerDiv.appendChild(div2);

    const span1 = document.createElement("span");
    span1.setAttribute("class", "txt-code");
    const span1Text = document.createTextNode(createCodeText(feature));

    span1.appendChild(span1Text);
    div2.appendChild(span1);

    const onceWikidata = once(fetchWikiData);

    onceWikidata(feature, outerDiv);

    featuresData[feature.id] = outerDiv;
    return outerDiv;
  }
}

function createNameText(feature) {
  if (feature.properties.name) {
    if (feature.properties.iso_a2 && (feature.properties.level === 0)) {
      return `${countries[feature.properties.iso_a2].flag} ${feature.properties.name}`;
    } else {
      return feature.properties.name
    }
  } else {
    return '';
  }
}

function createCodeText(feature) {
  if (feature.layer.id === 'postal-data') {
    return `${feature.properties.code}`;
  } else {
    return `${feature.properties.iso_a2 && (feature.properties.level === 0) ? feature.properties.iso_a2 : feature.properties.code}`;
  }
}

function fetchWikiData(feature, outerDiv) {
  if (feature.properties.wikidata) {
    const wikidata_id = feature.properties.wikidata;
    const div3 = document.createElement("div");
    div3.setAttribute('class', 'wikidata');
    outerDiv.appendChild(div3);

    setTimeout(
      fetchArticleFromWikidata, random(3, 10) * 100,
      wikidata_id, feature.properties.iso_a2, "en",
      (wikipediaJSON) => {
        div3.innerHTML = creeateWikipediaDescription(wikipediaJSON);
      }
    );
  } else if (feature.layer.id === 'administrative-data') {
    const level = feature.properties.level;
    let title;

    if (level === 1) {
      title = feature.properties.name;
    } else if (level == 2) {
      title = `${feature.properties.name}, ${feature.properties.level_1}`;
    }
    if (title) {
      const div3 = document.createElement("div");
      div3.setAttribute('class', 'wikidata');
      outerDiv.appendChild(div3);

      setTimeout(
        fetchArticleFromWikipedia, random(3, 10) * 100,
        title, "en",
        (wikipediaJSON) => {
          div3.innerHTML = creeateWikipediaDescription(wikipediaJSON)
        }
      );
    }
  }
}

function fetchArticleFromWikidata(qid, iso_a2, lang = 'en', cb) {
  const sparqlQuery = `SELECT ?country ?countryLabel ?population ?capitalLabel ?currencyLabel ?iso2 ?code ?article WHERE {
    ?country wdt:P31 wd:Q6256.
    ?country wdt:P17 wd:${qid}.
    ?country wdt:P1082 ?population.
    ?country wdt:P36 ?capital.
    ?country wdt:P38 ?currency.
    ?country wdt:P297 ?iso2.
    ?country wdt:P299 ?code.
    ?article schema:about ?country .
    ?article schema:isPartOf <https://${lang}.wikipedia.org/>.
    SERVICE wikibase:label { bd:serviceParam wikibase:language "${lang}". }
  }`;

  queryDispatcher.query(sparqlQuery).then((wikidataJSON) => {
    if (wikidataJSON.results?.bindings?.length > 1) {
      cb(tranformWikidataCountries(wikidataJSON.results?.bindings.find(item => item.iso2?.value === iso_a2)));
    } else if (wikidataJSON.results?.bindings?.[0]) {
      cb(tranformWikidataCountries(wikidataJSON.results.bindings[0]));
    }
  });
}

function tranformWikidataCountries(wikidata) {
  return {
    sitelink: wikidata.article?.value,
    capital: wikidata.capitalLabel?.value,
    currency: wikidata.currencyLabel?.value,
    population: wikidata.population?.value
  };
}

function fetchArticleFromWikipedia(title, lang = 'en', cb) {
  fetch(`https://${lang}.wikipedia.org/api/rest_v1/page/summary/${title}`, {
    headers: {
      "Content-Type": "application/json; charset=UTF-8",
      Origin: "https://labs.maptiler.com/countries"
    }
  }).then(response => response.json())
    .then(wikipediaJSON => {
      if (wikipediaJSON.content_urls?.desktop?.page) {
        cb({ sitelink: wikipediaJSON.content_urls?.desktop?.page });
      }
    });
}

function creeateWikipediaDescription(wikipediaJSON) {
  const properties = Object.keys(wikipediaJSON).map(item => {
    if (wikipediaJSON[item] && item !== 'sitelink') {
      return `<div class='label'><span>${item}:</span>${wikipediaJSON[item]}</div>`;
    }
  });
  properties.push(`<div class='wikipedia-link'><span>from <a class='link' href='${wikipediaJSON.sitelink}'>Wikipedia</a></div>`);
  return properties.join('');
}
