export const createContriesSelector = (containerId, countries, callback) => {
  const container = (containerId instanceof HTMLElement) ? containerId : document.getElementById(containerId);
  container.innerHTML = "";
  const inputContainer = document.createElement('div');
  inputContainer.className = 'input-group mb-3';
  const searchInput = document.createElement('input');
  searchInput.className = 'form-control';
  searchInput.setAttribute('type', 'text');
  searchInput.setAttribute('placeholder', 'Select a country');
  searchInput.setAttribute('aria-label', 'Select a country');
  searchInput.setAttribute('type', 'text');
  searchInput.setAttribute('type', 'text');
  const spanIcon = document.createElement('span');
  spanIcon.className = 'input-group-text';
  const searchIcon = document.createElement('i');
  searchIcon.className = 'bi bi-search';

  //searchInput.innerHTML = '<span class="input-group-text"><i class="bi bi-search"></i></span>';
  const listCountry = document.createElement('ul');
  listCountry.className = 'list-group shadow countriesList';

  Object.keys(countries).forEach(item => {
    const li = document.createElement('li');
    const {name, flag} = countries[item];
    li.className = 'list-group-item list-group-item-action';
    li.setAttribute('data-code', item);
    li.innerHTML = `${name} (${item}) ${flag}`;
    listCountry.appendChild(li);
  });

  spanIcon.appendChild(searchIcon);
  inputContainer.appendChild(searchInput);
  inputContainer.appendChild(spanIcon);
  container.appendChild(inputContainer);
  container.appendChild(listCountry);
  
  searchInput.addEventListener('keyup', (event) => {
    const filter = event.target.value.toUpperCase();
    const list = listCountry.getElementsByTagName('li');
    for (let i = 0; i < list.length; i++) {
      if (list[i].textContent.toUpperCase().indexOf(filter) > -1) {
        list[i].style.display = "";
      } else {
        list[i].style.display = "none";
      }
    }
  });
    
  searchInput.addEventListener('focus', (event) => {
    event.target.value = "";
    listCountry.style.display = 'block';
    const list = listCountry.getElementsByTagName('li');
    for (let i = 0; i < list.length; i++) {
      list[i].style.display = "";
    }
  });
  
  listCountry.addEventListener('click', (event) => {
    const countryCode = event.target.getAttribute("data-code");
    const selectedCountry = countries[countryCode];
    searchInput.value = event.target.textContent;
    listCountry.style.display = 'none';
    if (selectedCountry) {
      callback(selectedCountry, countryCode);
    }
  });

  document.addEventListener('click', function(event) {
    const isClickInsideElement = container.contains(event.target);
    if (!isClickInsideElement) {
        //Do something click is outside specified element
        listCountry.style.display = 'none';
    }
  });
};
