export async function getCountryList() {
  const response = await fetch('https://cdn.maptiler.com/data/countries/administrative-level-0.csv');
  const csv = await response.text();
  const countries = Papa.parse(csv, {
    header: true 
  });
  const countries_key = countries.data.reduce((agg, item) => {
    if (item.iso_a2 && item.iso_a2 !== 'NULL') {
      if (agg[item.iso_a2]) {
        if (countryDisambiguation(item)) {
          agg[item.iso_a2] = {...countries_flags[item.iso_a2], ...item, bbox: item.bbox.split(' ')};
        }
      } else {
        agg[item.iso_a2] = {...countries_flags[item.iso_a2], ...item, bbox: item.bbox.split(' ')};
      }
    }
    return agg;
  }, {});
  return countries_key;
}

function countryDisambiguation (item) {
  let mainCountry = false;
  switch (item.iso_a2) {
    case 'AU':
      mainCountry = caseAU(item);
      break;
    case 'ES':
      mainCountry = caseES(item);
      break;
    case 'FR':
      mainCountry = caseFR(item);
      break;
    case 'GB':
      mainCountry = caseGB(item);
      break;
    case 'NO':
      mainCountry = caseNO(item);
      break;
    case 'PT':
      mainCountry = casePT(item);
      break;
    case 'SA':
      mainCountry = caseSA(item);
      break;
    case 'UM':
      mainCountry = caseUM(item);
      break;
    case 'US':
      mainCountry = caseUS(item);
      break;
    default:
      break;
  }
  return mainCountry;
}

function caseAU(item) {
  return item.name === 'Australia';
}

function caseES(item) {
  return item.name === 'Spain';
}

function caseFR(item) {
  return item.name === 'France';
}

function caseGB(item) {
  return item.name === 'United Kingdom';
}

function caseNO(item) {
  return item.name === 'Norway';
}

function casePT(item) {
  return item.name === 'Portugal';
}

function caseSA(item) {
  return item.name === 'Saudi Arabia';
}

function caseUM(item) {
  return item.name === 'United States Minor Outlying Islands';
}

function caseUS(item) {
  return item.name === 'United States of America';
}

export function getUSMainBbox() {
  return countries_flags['US'].bbox;
}